@import url(https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;1,100;1,200;1,300;1,400;1,500;1,600;1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lora:ital@0;1&display=swap);
* {
    font-family: 'Raleway', sans-serif;
}

.Header .Menu{
    padding: 30px;
}

.Header .Menu #create {
    font-size: 40pt;
}

.Header {
    display: flex;
    flex-direction: column;
}

.Menu {
    background-color: #a2d1d2;
    background-image: linear-gradient(to bottom right, #67EFFF, #679FFF);
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    margin: 0;
    padding: 0;
    height: -webkit-max-content;
    height: -moz-max-content;
    height: max-content;
}

#create {
    font-weight: 500;
    text-transform: uppercase;
}

a {
    font-weight: 400;
    color: white;
    transition: 0.5s;
}

p {
    margin: 0;
}

a:link {
    text-decoration: none;
    font-size: 30pt;
}

a:hover {
    color: #001588;
}

.Logo {
    margin: 20px 0px 30px 0;
    align-self: center;
}

.Logo p {
    font-size: 35pt;
    font-family: 'Lora', serif;
    overflow: hidden;
    border-right: .1em solid transparent;
    white-space: nowrap;
    margin: 0 auto;
    letter-spacing: .09em;
    -webkit-animation:
            typing 2.5s steps(30, end),
            blink-caret .5s step-end 7;
            animation:
            typing 2.5s steps(30, end),
            blink-caret .5s step-end 7;
}

@-webkit-keyframes typing {
    from { width: 0 }
    to { width: 100% }
}

@keyframes typing {
    from { width: 0 }
    to { width: 100% }
}

@-webkit-keyframes blink-caret {
    from, to { border-color: transparent }
    50% { border-color: black }
}

@keyframes blink-caret {
    from, to { border-color: transparent }
    50% { border-color: black }
}

.Footer {
    background-color: #367b92;
    background-image: linear-gradient(to bottom right, #4499b7, #255361);
    display: flex;
    flex-direction: column;
    align-items: center;
    border: none;
}

.Footer p {
    font-size: 20pt;
}

.Footer .Menu {
    width: 100%;
    background-image: none;
    background-color: transparent;
    background-color: initial;
    margin: 20px 0 20px 0;
}

.Footer a {
    font-size: 20pt;
    transition: 0.4s;
}

.Footer a:hover {
    color: black;
}

.Footer .Credit {
    font-size: 14pt;
}

.Footer .Credits {
    display: flex;
    flex-direction: row;
    color: white;
}

.Footer .Year {
    margin-bottom: 10px;
    color: white;
    font-weight: 600;
}

.Test {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    font-size: 20pt;
    margin: 20px 0 20px 0;
}

